<template>
    <v-container class="pa-2">
        <div class="row-header no__hidden_class">
            <div class="background-triangle-big">
                <span>DANH SÁCH CÔNG DÂN</span>
            </div> 
        </div>
        <v-layout wrap class="white pt-3">
            <v-flex xs12>
                <v-layout wrap>
                    <v-flex xs12 md3 class="px-2">
                        <v-text-field
                            label="CMND"
                            v-model="applicantIdNo"
                            box
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md4 class="px-2">
                        <v-text-field
                            label="Tên công dân"
                            box
                            v-model="applicantName"
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md4 class="px-2">
                        <v-text-field
                            label="Email"
                            box
                            v-model="contactEmail"
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12 md1>
                      <v-btn fab dark small color="primary" @click="getList()">
                        <v-icon dark>filter_list</v-icon>
                      </v-btn>
                    </v-flex>
                </v-layout>
            </v-flex>
            <v-flex xs12 class="px-2">
                <v-data-table
                    id="table-dossier"
                    no-data-text="Không có dữ liệu"
                    hide-actions
                    item-key="dossierId"
                    class="table-landing table-bordered"
                    :headers="headers"
                    :items="desserts"
                >
                  <template slot="headers" slot-scope="props">
                    <tr>
                      <th v-for="header in props.headers" :key="header.text">{{header.text}}</th>
                    </tr>
                  </template>
                    <template slot="items" slot-scope="props">
                            <td>{{ props.index + 1 }}</td>
                            <td>{{ props.item.applicantName }}</td>
                            <td>{{ props.item.applicantIdNo }}</td>
                            <td>{{ props.item.contactEmail }}</td>
                            <td><v-btn small color="primary" dark @click="active(props.item.applicantId)">Kích hoạt</v-btn></td> 
                    </template>
                </v-data-table>
                <TinyPagination></TinyPagination>
            </v-flex>
        </v-layout> 

    </v-container>
</template>

<script>
    import axios from 'axios'
    import TinyPagination from './TinyPagination'
    export default {
        components: {
           TinyPagination 
        },
        data () {
            return {
              applicantName: '',
              applicantIdNo: '',
              contactEmail: '',
              headers: [
                  { text: 'STT', align: 'left', sortable: false},
                  { text: 'Tên công dân', sortable: false },
                  { text: 'Số CMND', sortable: false },
                  { text: 'Email', sortable: false  },
                  { text: 'Thao tác', sortable: false },
              ],
              desserts: []
            }
        },
        created () {
          let vm = this
          vm.$nextTick(function(){
            vm.getList()
          })
        },
        methods: {
          getList () {
            let vm = this
            let config = {
              headers: {
                'groupId': window.themeDisplay.getScopeGroupId(),
              }
            }
            let applicantNameString = '&applicantName=' + vm.applicantName
            let applicantIdNoString = '&applicantIdNo=' + vm.applicantIdNo
            let contactEmailString= '&contactEmail=' + vm.contactEmail

            let applicantName = vm.applicantName.length > 3 ? applicantNameString : ''
            let applicantIdNo = vm.applicantIdNo.length > 3 ? applicantIdNoString : ''
            let contactEmail = vm.contactEmail.length > 3 ? contactEmailString : ''

            let dataPost = new URLSearchParams()
            dataPost.append('method', 'GET')
            dataPost.append('data', '')
            dataPost.append('url', '/applicants?verification=1&haveAccount=true' + applicantName + applicantIdNo + contactEmail)
            axios.post('/o/rest/v2/proxy', dataPost, config).then(function (result1) {
              if (result1.data) {
                vm.desserts = result1.data.data
              } else {
                vm.desserts = []
              }
            }).catch(() => {
              
            })
          },
          active (applicantId) {
          
            let config = {
              headers: {
                'groupId': window.themeDisplay.getScopeGroupId(),
              }
            }
            let textPost = {
              verification: 1,
              haveAccount: true
            }
            let dataPost = new URLSearchParams()
            dataPost.append('method', 'POST')
            dataPost.append('data',  JSON.stringify(textPost))
            dataPost.append('url', '/verify/' + applicantId)
            axios.post('/o/rest/v2/proxy', dataPost, config).then(function () {
            }).catch(() => {
            })
          }
        }

    }
</script>

